.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  clip: rect(0 0 0 0);
  overflow: hidden; }

.page-body {
  min-height: 100%;
  width: 100%;
  min-width: 320px;
  background-color: #ffffff;
  font-family: "Poppins", "Arial", sans-serif;
  color: #1d1613; }

.header {
  box-sizing: border-box;
  padding: 13px calc(45px + (98 - 45) * ((100vw - 1024px) / (1366 - 1024))) 19px;
  display: flex;
  align-items: center;
  justify-content: space-between; }

.header__logo {
  margin: 17px 0 0; }

.header__list-menu {
  display: flex;
  list-style-type: none; }

.header__item-menu a {
  font-weight: 500;
  font-size: 14px;
  line-height: 21px;
  text-decoration: none;
  color: #1d1613; }

@media (max-width: 1023px) {
  .header {
    padding: 13px calc(15px + (45 - 15) * ((100vw - 768px) / (1023 - 768))) 19px; } }

@media (max-width: 767px) {
  .header {
    padding: 13px 15px 19px; } }

.form-search input {
  font-weight: 500;
  font-size: 14px;
  line-height: 21px; }

.form-search__btn {
  border: none;
  background-color: transparent; }

.navigation {
  box-sizing: border-box;
  padding: 25px; }

.navigation__list {
  display: flex;
  align-items: center;
  justify-content: center;
  list-style-type: none;
  margin: 0; }

.navigation__item a {
  font-weight: 500;
  font-size: 16px;
  line-height: 24px;
  text-decoration: none;
  color: #1d1613;
  margin: 0 17.5px 0; }

.footer {
  background-color: #000000;
  box-sizing: border-box;
  padding: 100px calc(45px + (98 - 45) * ((100vw - 1024px) / (1366 - 1024)));
  display: flex;
  align-items: center; }

.footer__wrapper-form h2 {
  font-family: "Cormorant Garamond", "Arial", serif;
  font-weight: bold;
  font-size: 34px;
  line-height: 130%;
  color: #ffffff; }

.footer__wrapper-form p {
  font-weight: normal;
  font-size: 16px;
  line-height: 130%;
  color: #9b9997; }

@media (max-width: 1023px) {
  .footer {
    padding: 0 calc(15px + (45 - 15) * ((100vw - 768px) / (1023 - 768))); } }

@media (max-width: 767px) {
  .footer {
    padding: 0 15px; } }

.form-registration {
  display: flex;
  align-items: baseline;
  justify-content: space-between;
  border-bottom: 1px solid #ffffff; }
  .form-registration input {
    border: none;
    background-color: transparent; }

.form-registration__btn-sing-up {
  box-sizing: border-box;
  border: none;
  background-color: transparent; }
  .form-registration__btn-sing-up span {
    font-weight: 500;
    font-size: 16px;
    line-height: 130%;
    color: #ffffff; }

.footer-navigation {
  display: flex; }

.footer-navigation__list {
  list-style-type: none; }

.footer-navigation__item a {
  font-weight: 500;
  font-size: 16px;
  line-height: 130%;
  text-decoration: none;
  color: #ffffff; }
